"use client";

import React, { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { ChevronDown } from "lucide-react";
import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import uPlot from "uplot";
import "uplot/dist/uPlot.min.css";

/**************************** Config jauges ****************************/
interface GaugeConfig {
  min: number;
  max: number;
  alarmLow?: number;
  alarmHigh?: number;
  unit?: string;
}

const CONFIGS: Record<string, GaugeConfig> = {
  "TOOL TEMP": { min: 0, max: 250, alarmLow: 10, alarmHigh: 220, unit: "°C" },
  "TOOL TEMP DEVICE": { min: 0, max: 120, alarmLow: 5, alarmHigh: 100, unit: "°C" },
  "HEAD TEMPERATURE N°1": { min: 0, max: 250, alarmLow: 10, alarmHigh: 220, unit: "°C" },
  "HEAD TEMPERATURE N°2": { min: 0, max: 250, alarmLow: 10, alarmHigh: 220, unit: "°C" },
  FORCE: { min: 0, max: 20000, alarmHigh: 18000, unit: "N" },
  "Z POSITION": { min: -50, max: 50, unit: "mm" },
  "ROTATION SPEED": { min: 0, max: 11000, alarmHigh: 10000, unit: "rpm" },
};

const OPTIONS = Object.keys(CONFIGS);

/**************************** Mapping PLC ****************************/
const API = process.env.NEXT_PUBLIC_BACKEND_URL ?? "http://localhost:3001";

type PlcType = "REAL" | "UDINT" | string;
const PLC: Record<string, { name: string; type: PlcType; scale?: number }> = {
  "TOOL TEMP": { name: "GVL_Public.g_tool_temp", type: "REAL" },
  "TOOL TEMP DEVICE": { name: "GVL_Public.g_tool_device", type: "REAL" },
  "HEAD TEMPERATURE N°1": { name: "GVL_Public.real_g_head_temperature_n1", type: "REAL" },
  "HEAD TEMPERATURE N°2": { name: "GVL_Public.g_head_temperature_n2", type: "REAL" },
  FORCE: { name: "GVL_Public.g_force", type: "REAL" },
  "Z POSITION": { name: "GVL_Public.g_ZPos", type: "REAL" },
  "ROTATION SPEED": { name: "GVL_Public.g_rotation_speed", type: "UDINT" },
};

/**************************** Types ****************************/
interface Point { t: number; v: number }

type TimeScale = "s" | "m" | "h";

/**************************** Fréquences & limites ****************************/
// FPS cible pour le rendu (React ne fait qu'invalider, uPlot dessine en Canvas)
const DEFAULT_TARGET_FPS = 60; // 30 ou 60

// Échantillonnage historique (push en buffer)
const SAMPLE_WHEN_VISIBLE_MS = 100; // 10 Hz (fluidité sans bruit réseau)
const SAMPLE_WHEN_HIDDEN_MS = 1000; // 1 Hz onglet masqué

// Historique conservé (points)
const HISTORY_LIMIT = 600; // 60 s à 10 Hz

/**************************** Ring buffer optimisé (no-copy + extrema) ****************************/
function createRingBuffer(limit: number) {
  const buf: Point[] = [];
  let max = -Infinity, min = Infinity, ver = 0;

  function recomputeExtrema() {
    max = -Infinity; min = Infinity;
    for (let i = 0; i < buf.length; i++) {
      const v = buf[i].v;
      if (v > max) max = v;
      if (v < min) min = v;
    }
  }

  return {
    push(p: Point) {
      if (buf.length < limit) {
        buf.push(p);
      } else {
        const removed = buf.shift()!;
        buf.push(p);
        if (removed.v === max || removed.v === min) recomputeExtrema();
      }
      if (p.v > max) max = p.v;
      if (p.v < min) min = p.v;
      ver++;
    },
    clearAndSeed(p: Point) { buf.length = 0; max = min = p.v; buf.push(p); ver++; },
    view() { return buf; },                     // même référence, pas de copie
    stats() { return { max, min, ver, len: buf.length }; },
  } as const;
}

/**************************** Helper JSON sûr ****************************/
async function safeJson(r: Response) {
  const ct = r.headers.get("content-type") || "";
  const text = await r.text();
  if (ct.includes("application/json")) {
    try { return JSON.parse(text); }
    catch (e) { console.error("JSON parse error:", e, text.slice(0, 200)); throw new Error("Bad JSON from backend"); }
  }
  console.error("Expected JSON, got:", ct || "unknown", "payload:", text.slice(0, 200));
  throw new Error("Backend returned non-JSON (HTML?)");
}

/**************************** Composant uPlot (Canvas ultra fluide) ****************************/
function UplotLine({
  dataRef,     // () => Point[] (référence stable du ring buffer)
  yMin,
  yMax,
  timeFormatter,
  height = 360,
}: {
  dataRef: () => Point[];
  yMin: number;
  yMax: number;
  timeFormatter: (t: number) => string;
  height?: number;
}) {
  const elRef = useRef<HTMLDivElement | null>(null);
  const plotRef = useRef<uPlot | null>(null);
  const xsRef = useRef<number[]>([]);
  const ysRef = useRef<number[]>([]);
  const verRef = useRef<number>(-1); // version déjà rendue

  // Création une fois
  useEffect(() => {
    if (!elRef.current) return;
    const opts: uPlot.Options = {
      width: elRef.current.clientWidth,
      height,
      scales: { x: { time: false }, y: { auto: false } },
      axes: [
        { grid: { show: true }, values: (_, vals) => vals.map(v => timeFormatter(Number(v))) },
        { grid: { show: true } },
      ],
      series: [
        {},
        { stroke: "#0ea5e9", width: 2 }, // série Y (couleur système)
      ],
      // options perf
      hooks: {
        ready: [() => { verRef.current = -1; }],
      },
    };

    plotRef.current = new uPlot(opts, [xsRef.current, ysRef.current], elRef.current);

    const ro = new ResizeObserver(() => {
      if (!plotRef.current || !elRef.current) return;
      plotRef.current.setSize({ width: elRef.current.clientWidth, height });
    });
    ro.observe(elRef.current);

    return () => { ro.disconnect(); plotRef.current?.destroy(); plotRef.current = null; };
  }, [height, timeFormatter]);

  // Mise à jour des données (référence inchangée => on lit directement)
  const sync = useCallback((ver: number) => {
    if (!plotRef.current) return;
    const data = dataRef();

    // Si on a déjà rendu cette version, skip
    if (verRef.current === ver) return;
    verRef.current = ver;

    const n = data.length;
    xsRef.current.length = n;
    ysRef.current.length = n;
    for (let i = 0; i < n; i++) {
      xsRef.current[i] = data[i].t; // t en secondes (voir page principale)
      ysRef.current[i] = data[i].v;
    }

    plotRef.current.setData([xsRef.current, ysRef.current], false);
  }, [dataRef]);

  // Mise à jour de l'échelle Y
  useEffect(() => {
    if (!plotRef.current) return;
    plotRef.current.setScale("y", { min: yMin, max: yMax });
  }, [yMin, yMax]);

  return <div ref={elRef} className="w-full h-[360px]" />;
}

/**************************** Page ****************************/
export default function DashboardPage() {
  // Sélection métrique
  const [selected, setSelected] = useState<string>(OPTIONS[0]);
  const selectedRef = useRef(selected);
  useEffect(() => { selectedRef.current = selected; }, [selected]);

  // Valeurs affichées (RAF)
  const [displayValues, setDisplayValues] = useState<Record<string, number>>(
    () => Object.fromEntries(OPTIONS.map(k => [k, 0])) as Record<string, number>
  );

  // Valeurs brutes en ref
  const liveValuesRef = useRef<Record<string, number>>(
    Object.fromEntries(OPTIONS.map(k => [k, 0])) as Record<string, number>
  );

  // Buffers par métrique (1 seule fois)
  const buffersRef = useRef<Record<string, ReturnType<typeof createRingBuffer>> | null>(null);
  if (!buffersRef.current) {
    buffersRef.current = Object.fromEntries(OPTIONS.map(k => [k, createRingBuffer(HISTORY_LIMIT)])) as Record<string, ReturnType<typeof createRingBuffer>>;
  }

  // Flags
  const [isReady, setIsReady] = useState(false);
  const [timeScale, setTimeScale] = useState<TimeScale>("s");
  const [error, setError] = useState<string | null>(null);
  const [targetFps, setTargetFps] = useState<number>(DEFAULT_TARGET_FPS);

  const cfg = CONFIGS[selected];
  const value = displayValues[selected] ?? 0;
  const unit = cfg.unit ?? "";

  /************ jauge ************/
  const { percentage, pathColor, min, max } = useMemo(() => {
    const span = cfg.max - cfg.min || 1;
    let pct = ((value - cfg.min) / span) * 100;
    pct = Math.min(100, Math.max(0, pct));
    const hi = cfg.alarmHigh !== undefined && value >= cfg.alarmHigh;
    const lo = cfg.alarmLow !== undefined && value <= cfg.alarmLow;
    const color = hi ? "#ef4444" : lo ? "#f59e0b" : "#10b981";
    return { percentage: pct, pathColor: color, min: cfg.min, max: cfg.max };
  }, [cfg, value]);

  /************ Seed à chaque changement de métrique ************/
  useEffect(() => {
    setIsReady(false);
    setError(null);
    const map = PLC[selected];
    let cancelled = false;

    if (!map) { setIsReady(true); return; }

    (async () => {
      try {
        const url = `${API}/api/read?name=${encodeURIComponent(map.name)}&type=${encodeURIComponent(map.type)}`;
        const r = await fetch(url, { cache: "no-store" });
        if (!r.ok) throw new Error(`seed read failed: ${r.status}`);
        const data = await safeJson(r);
        const raw = Number((data as any)?.value);
        if (!Number.isFinite(raw)) throw new Error("seed NaN");
        const v0 = (map.scale ?? 1) * raw;
        if (cancelled) return;

        liveValuesRef.current[selected] = v0;
        const nowS = performance.now() / 1000; // temps monotone en secondes
        const buf = buffersRef.current![selected];
        buf.clearAndSeed({ t: nowS - SAMPLE_WHEN_VISIBLE_MS / 1000, v: v0 });
        buf.push({ t: nowS, v: v0 });
        setIsReady(true);
      } catch (e: any) {
        console.error(e);
        if (!cancelled) setError(e?.message || "Erreur seed");
      }
    })();

    return () => { cancelled = true; };
  }, [selected]);

  /************ Polling PLC (maj ref, sans re-render) ************/
  useEffect(() => {
    let stop = false;
    let timer: ReturnType<typeof setTimeout> | null = null;

    const schedule = (ms: number) => { if (!stop) timer = setTimeout(pollOnce, ms); };

    const pollOnce = async () => {
      const key = selectedRef.current;
      const map = PLC[key];
      if (!map) return schedule(document.hidden ? 1000 : 200);
      try {
        const url = `${API}/api/read?name=${encodeURIComponent(map.name)}&type=${encodeURIComponent(map.type)}`;
        const r = await fetch(url, { cache: "no-store" });
        if (r.ok) {
          try {
            const data = await safeJson(r);
            const raw = Number((data as any)?.value);
            if (Number.isFinite(raw)) {
              liveValuesRef.current[key] = (map.scale ?? 1) * raw;
            }
          } catch (e) {
            console.warn("poll JSON issue", e);
          }
        } else {
          console.warn("poll status", r.status);
        }
      } catch (e) {
        console.warn("poll error", e);
      }
      schedule(document.hidden ? 1000 : 200);
    };

    pollOnce();
    return () => { stop = true; if (timer) clearTimeout(timer); };
  }, []);

  /************ Sampling -> push historique (thread UI très léger) ************/
  useEffect(() => {
    let stop = false;
    let timer: ReturnType<typeof setTimeout> | null = null;

    const tick = () => {
      if (stop) return;
      if (isReady) {
        const key = selectedRef.current;
        const v = liveValuesRef.current[key] ?? 0;
        const nowS = performance.now() / 1000;
        buffersRef.current![key].push({ t: nowS, v });
      }
      const ms = document.hidden ? SAMPLE_WHEN_HIDDEN_MS : SAMPLE_WHEN_VISIBLE_MS;
      timer = setTimeout(tick, ms);
    };

    tick();
    return () => { stop = true; if (timer) clearTimeout(timer); };
  }, [isReady]);

  /************ Boucle de rendu (requestAnimationFrame) ************/
  const [uiTick, setUiTick] = useState(0);
  useEffect(() => {
    let id = 0;
    let last = 0;

    const loop = (t: number) => {
      if (!document.hidden) {
        const targetMs = 1000 / targetFps;
        if (t - last >= targetMs) { setUiTick(v => v + 1); last = t; }
      }
      id = requestAnimationFrame(loop);
    };

    id = requestAnimationFrame(loop);
    return () => cancelAnimationFrame(id);
  }, [targetFps]);

  // Sync valeur affichée (métrique sélectionnée)
  useEffect(() => {
    const key = selected;
    setDisplayValues(prev => ({ ...prev, [key]: liveValuesRef.current[key] ?? 0 }));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [uiTick, selected]);

  /************ Données chart + axes (no-copy) ************/
  const buf = buffersRef.current![selected];
  const { max: maxObs, min: minObs, ver } = buf.stats();
  const historyView = buf.view; // fonction stable

  // t0 = premier point en secondes monotones
  const t0 = useMemo(() => {
    const d = historyView();
    return d.length ? d[0].t : performance.now() / 1000;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selected]);

  const timeUnitLabel = timeScale === "s" ? "s" : timeScale === "m" ? "min" : "h";
  const timeFormatter = useCallback((t: number) => {
    const dt = Math.max(0, t - t0); // secondes
    if (timeScale === "s") return `${dt.toFixed(1)}`;
    if (timeScale === "m") return `${(dt / 60).toFixed(2)}`;
    return `${(dt / 3600).toFixed(3)}`;
  }, [t0, timeScale]);

  // Échelle Y très stable: privilégie les limites config; garde un léger headroom basé sur maxObs
  const [yMin, yMax] = useMemo<[number, number]>(() => {
    const c = CONFIGS[selected];
    const headroom = 1.05;
    const upper = Math.min(c.max, (Number.isFinite(maxObs) ? maxObs : c.max) * headroom || c.max);
    const lower = c.min >= 0 ? 0 : c.min;
    // borne minimale si pas de points
    const u = upper <= lower ? c.max : upper;
    return [lower, u];
  }, [selected, maxObs]);

  return (
    <div className="h-full w-full p-8 bg-gray-100 flex flex-col gap-6">
      {error && (
        <div className="bg-red-50 text-red-700 border border-red-200 rounded-lg p-3 text-sm">
          <b>Erreur lecture:</b> {error}
        </div>
      )}

      <div className="w-full bg-white rounded-2xl p-6 shadow-lg flex flex-col md:flex-row gap-6">
        {/* --- Colonne jauge --- */}
        <div className="md:w-1/4 flex flex-col items-center">
          <div className="relative w-full md:w-4/5">
            <select
              className="w-full border border-gray-300 rounded px-3 py-2 text-center text-lg font-bold appearance-none"
              value={selected}
              onChange={(e) => setSelected(e.currentTarget.value)}
            >
              {OPTIONS.map((opt) => (
                <option key={opt}>{opt}</option>
              ))}
            </select>
            <ChevronDown size={20} className="absolute top-1/2 right-3 -translate-y-1/2 text-gray-500 pointer-events-none" />
          </div>

          <div className="mt-6 w-full max-w-xs h-[140px] overflow-hidden">
            <CircularProgressbar
              value={percentage}
              maxValue={100}
              circleRatio={0.5}
              strokeWidth={5}
              text=""
              styles={buildStyles({ rotation: 0.75, pathColor, trailColor: "#e5e7eb", strokeLinecap: "butt" })}
            />
          </div>

          <div className="-mt-12 text-center select-none pointer-events-none">
            <span className="text-4xl font-bold text-[#071039]">
              {value.toFixed(1)}
              {unit ? <span className="ml-1 text-base font-medium text-gray-500">{unit}</span> : null}
            </span>
          </div>

          <div className="mt-1 flex justify-between w-full md:w-4/5 text-xs text-gray-500 font-medium">
            <span>{min}</span>
            <span>{max}</span>
          </div>

          <div className="mt-4 flex items-center gap-2 text-sm text-gray-600">
            <span>FPS:</span>
            {[30, 60].map(f => (
              <button key={f} onClick={() => setTargetFps(f)} className={`px-2 py-1 rounded border ${targetFps === f ? "bg-gray-200" : "bg-white"}`}>{f}</button>
            ))}
          </div>

          <div className="mt-2 flex items-center gap-2 text-sm text-gray-600">
            <span>Échelle temps:</span>
            <button onClick={() => setTimeScale("s")} className={`px-2 py-1 rounded ${timeScale === "s" ? "bg-gray-200" : ""}`}>s</button>
            <button onClick={() => setTimeScale("m")} className={`px-2 py-1 rounded ${timeScale === "m" ? "bg-gray-200" : ""}`}>min</button>
            <button onClick={() => setTimeScale("h")} className={`px-2 py-1 rounded ${timeScale === "h" ? "bg-gray-200" : ""}`}>h</button>
          </div>
        </div>

        {/* --- Colonne graph --- */}
        <div className="md:w-3/4 h-[360px]">
          {/* uPlot lit directement la vue du buffer; pas de copie */}
          <UplotLine
            dataRef={buffersRef.current![selected].view}
            yMin={yMin}
            yMax={yMax}
            timeFormatter={(x) => {
              // x = secondes monotones; formattage relatif selon timeScale
              const dt = Math.max(0, x - (buffersRef.current![selected].view()[0]?.t ?? x));
              if (timeScale === "s") return dt.toFixed(1);
              if (timeScale === "m") return (dt / 60).toFixed(2);
              return (dt / 3600).toFixed(3);
            }}
            height={360}
          />
        </div>
      </div>

      {/* Slider de test (désactivé si la métrique est lue en live) */}
      <input
        type="range"
        min={CONFIGS[selected].min}
        max={CONFIGS[selected].max}
        value={value}
        onChange={(e) => {
          const v = parseFloat(e.currentTarget.value);
          liveValuesRef.current[selected] = v;
          setDisplayValues((prev) => ({ ...prev, [selected]: v }));
        }}
        className="w-full"
        disabled={!!PLC[selected]}
      />
    </div>
  );
}